<template>
  <div class="container-accound">
    <div class="top" style="height:10%">
      <div>
        <h2>客流数量</h2>
      </div>
      <div>
        <span style="float:left">东风日产 北京 北京</span>
        <span style="float:right">日期选择 : 只能选周</span>
      </div>
    </div>
    <div class="echarts" style="height:90%">
      <div id="chart" class="chart" style="width: 100%;height:100%;"></div>
    </div>
  </div>
</template>

<script>
export default {
  mounted () {
    this.chart()
  },
  methods: {
    chart () {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById('chart'))
      var dataval = [2220, 1682, 2791, 3000, 4090, 3230, 2910]
      var xAxisval = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat']
      // var dataval1 = [0, 0, 0, 0, 0, 0, 0]
      myChart.setOption({
        backgroundColor: '#293042',
        grid: {
          left: 10,
          top: '10%',
          bottom: 20,
          right: 40,
          containLabel: true
        },
        tooltip: {
          show: true,
          backgroundColor: '#384157',
          borderColor: '#384157',
          borderWidth: 1,
          formatter: '{b}:{c}',
          extraCssText: 'box-shadow: 0 0 5px rgba(0, 0, 0, 1)'
        },
        legend: {
          right: 0,
          top: 0,
          data: ['距离'],
          textStyle: {
            color: '#5c6076'
          }
        },
        // title: {
        //   text: '东风日产 北京 北京',
        //   x: '4.5%',
        //   top: '1%',
        //   textStyle: {
        //     color: '#5c6076'
        //   }
        // },
        xAxis: {
          data: xAxisval,
          boundaryGap: false,
          axisLine: {
            show: false
          },
          axisLabel: {
            textStyle: {
              color: '#5c6076'
            }
          },
          axisTick: {
            show: false
          }
        },
        yAxis: {
          ayisLine: {
            show: false
          },
          axisLabel: {
            textStyle: {
              color: '#5c6076'
            }
          },
          splitLine: {
            show: true,
            lineStyle: {
              color: '#2e3547'
            }
          },
          axisLine: {
            lineStyle: {
              color: '#384157'
            }
          }
        },
        series: [
          {
            type: 'scatter',
            name: 'linedemo',
            tooltip: {
              show: false
            },
            animation: false,
            barWidth: 1.4,
            hoverAnimation: false,
            data: dataval,
            itemStyle: {
              normal: {
                color: '#f17a52',
                opacity: 0.6,
                label: {
                  show: false
                }
              }
            }
          },
          // {
          //   type: 'line',
          //   name: '距离',
          //   animation: false,
          //   symbol: 'circle',

          //   hoverAnimation: false,
          //   data: dataval1,
          //   itemStyle: {
          //     normal: {
          //       color: '#f17a52',
          //       opacity: 0
          //     }
          //   },
          //   lineStyle: {
          //     normal: {
          //       width: 1,
          //       color: '#384157',
          //       opacity: 1
          //     }
          //   }
          // },
          {
            type: 'line',
            name: 'linedemo',
            smooth: true,
            symbolSize: 10,
            animation: false,
            lineWidth: 1.2,
            hoverAnimation: false,
            data: dataval,
            symbol: 'circle',
            itemStyle: {
              normal: {
                color: '#f17a52',
                shadowBlur: 40,
                label: {
                  show: true,
                  position: 'top',
                  textStyle: {
                    color: '#f17a52',
                    fontSize: 18
                  }
                }
              }
            },
            areaStyle: {
              normal: {
                color: '#f17a52',
                opacity: 0.08
              }
            }
          }
        ]
      })
    }
  }
}
</script>

<style scoped lang='less'>
.container-accound {
  width: 100%;
  height: 100%;
}
.top {
  color: #fff;
  div:nth-child(1) {
    width: 100%;
    height: 50%;
    background: #293c55;
    h2 {
      padding-left:30px ;
      line-height: 50px;
      font-weight: 500;
      margin: 0;
    }
  }
  div:nth-child(2) {
    width: 100%;
    height: 50%;
    background: #293042;
    span {
      font-size: 20px;
    }
    span:nth-child(1) {
      padding: 10px 0 0 30px;
    }
    span:nth-child(2) {
      padding: 10px 30px 0 0;
    }
  }
}
.echarts {
  position: relative;
  .chart {
    position: absolute;
    width: 100%;
    height: 100%;
  }
}
</style>
