                { name: '东风南方大兴', coord: [116.330179, 39.749004], value: '北京市大兴区黄村镇清源路40号（仁和医院西行第二个红绿灯路南）' },
                { name: '东风南方亮马', coord: [116.484817, 39.960803], value: '北京市朝阳区亮马桥路25号' },
                {
                  name: '北京东风南方三合专营店',
                  coord: [116.514697, 39.818058],
                  value: '北京经济技术开发区北环东路乙15号' },
                { name: '日产福源', coord: [116.53139, 39.947202], value: '北京市朝阳区姚家园路东口甲1号东方基业汽车城J区14号' },
                { name: '北京航天正通', coord: [116.2468, 39.93221], value: '北京市海淀区阜石路61号' },
                { name: '北京宏远信诚', coord: [116.553246, 39.869763], value: '北京市朝阳区五方桥西南侧（欢乐谷东行3公里五方天涯汽配城东门）)' },
