                { name: '汇京鸿运日产', coord: [116.315548, 39.863359], value: '北京市丰台区丰管路16号院11号楼' },
                { name: '北京冀东兴', coord: [116.184973, 39.922582], value: '北京市石景山区古城大街国际汽车贸易服务园区F区17号原标致店' },
                { name: '北京晟通', coord: [116.108162, 39.727249], value: '北京市房山区阎村镇前沿村南京周路北澎湃汽车城内' },
                { name: '北京祥瑞聚鑫', coord: [116.224466, 40.206056], value: '北京市昌平区马池口镇百泉庄652号（八达岭高速科技园出口西800米）' },
