<template>
  <div class="container-accound">
    <div class="top" style="height:10%">
      <div>
        <h2>客流量排名</h2>
      </div>
      <div>
        <span style="float:left">东风日产 北京 北京</span>
        <span style="float:right">日期选择 : 只能选月</span>
      </div>
    </div>
    <div class="echarts" style="height:90%">
      <div id="chart" class="chart" style="width: 100%;height:100%;"></div>
    </div>
  </div>
</template>

<script>
import echarts from 'echarts'
export default {
  mounted () {
    this.chart()
  },
  methods: {
    chart () {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById('chart'))
      myChart.setOption({
        backgroundColor: '#293042',
        grid: {
          top: '15%',
          right: '10%',
          left: '10%',
          bottom: '12%'
        },
        xAxis: [
          {
            type: 'category',
            color: '#59588D',
            data: ['2016', '2017', '2018', '2019', '2020'],
            axisLabel: {
              margin: 20,
              color: '#999',
              textStyle: {
                fontSize: 18
              }
            },
            axisLine: {
              lineStyle: {
                color: '#d2d2d2'
              }
            },
            axisTick: {
              show: false
            }
          }
        ],
        yAxis: [
          {
            min: 0,
            max: 100,
            axisLabel: {
              formatter: '{value}%',
              color: '#999',
              textStyle: {
                fontSize: 18
              }
            },
            axisLine: {
              lineStyle: {
                color: 'rgba(107,107,107,0.37)'
              }
            },
            axisTick: {
              show: false
            },
            splitLine: {
              lineStyle: {
                color: 'rgba(131,101,101,0.2)',
                type: 'dashed'
              }
            }
          }
        ],
        series: [
          {
            type: 'bar',
            data: [50, 90, 10, 20, 56],
            barWidth: '50px',
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(
                  0,
                  0,
                  0,
                  1,
                  [
                    {
                      offset: 0,
                      color: '#ffffff' // 100% 处的颜色
                    },
                    {
                      offset: 1,
                      color: 'rgba(255, 255, 255, .1)' // 0% 处的颜色
                    }
                  ],
                  false
                ),
                barBorderRadius: [30, 30, 0, 0]
              }
            },
            label: {
              show: true,
              fontSize: 18,
              fontWeight: 'bold',
              position: 'top',
              color: 'block'
            }
          }
        ]
      })
    }
  }
}
</script>

<style scoped lang='less'>
.container-accound {
  position: relative;
  width: 100%;
  height: 100%;
}
.top {
  color: #fff;
  div:nth-child(1) {
    width: 100%;
    height: 50%;
    background: #293c55;
    h2 {
      padding-left: 30px;
      line-height: 50px;
      font-weight: 500;
      margin: 0;
    }
  }
  div:nth-child(2) {
    width: 100%;
    height: 50%;
    background: #293042;
    span {
      font-size: 20px;
    }
    span:nth-child(1) {
      padding: 10px 0 0 30px;
    }
    span:nth-child(2) {
      padding: 10px 30px 0 0;
    }
  }
}
.echarts {
  position: relative;
  .chart {
    position: absolute;
    width: 100%;
    height: 100%;
  }
}
</style>
